
import { defineComponent, ref } from 'vue'
import { useStore } from 'vuex'
import useFetching, { FetchStatus, ResponseFetching } from '@/hooks/useFetching'
import { IInformationForm } from '@/types'
import PageCard from '@/components/PageCard.vue'
import InformationForm from '@/views/catalog/information/forms/InformationForm.vue'

export interface IData {
  model: IInformationForm | null;
}

export default defineComponent({
  setup () {
    const store = useStore()

    const [saveForm, saveStatus, saveError] = useFetching((dataForm: IInformationForm) => {
      return store.dispatch('information/createForm', dataForm)
    }) as ResponseFetching<Promise<IInformationForm>>

    const form = ref()

    return {
      saveForm,
      saveStatus,
      saveError,
      form
    }
  },

  components: {
    PageCard,
    InformationForm
  },

  computed: {
    isLoading (): boolean {
      return this.saveStatus === FetchStatus.pending
    }
  },

  data (): IData {
    return {
      model: {
        title: 'New information',
        description: '',
        metaTitle: '',
        metaDescription: '',
        metaKeyword: '',
        isActive: true,
        sortOrder: 0
      }
    }
  },

  methods: {
    handleBack () {
      this.$router.push({ name: 'information' })
    },

    async handleSubmit () {
      // if (this.form && await this.form.validate()) {
      // }
      const data: IInformationForm = await this.saveForm(this.model)
      if (typeof data !== 'undefined') {
        this.model = data
      }

      if (this.model?.id) {
        this.$router.push({ name: 'information-update', params: { id: this.model.id } })
      }
    }
  }
})
